import React from 'react';
import './DiscoveryResult.css';

function DiscoveryResult(props) {
  return (
    <div className="result">
      <div className="result__title">{props.movieNm}({props.movieNmEn})</div>
      <div className="result__preview">:{props.nationAlt} :{props.genreAlt}</div>
      <div className="result__preview">:{props.directors} :{props.companyNm} :{props.openDt}</div>
      <div><a href={props.Url}></a></div>
    </div> 
  );
}

export default DiscoveryResult;



